.menu__primary.wp-block-pm-menu-fetcher {

	z-index: 2;
  	position: relative;

	& > ul.menu {
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		flex-direction: row;
		justify-content: center;
		li.menu-item:hover, li.current-menu-item {
			a { color: var(--wp--preset--color--primary) !important }
		}
		& > li.menu-item {
		
			padding: var(--wp--preset--spacing--20) var(--wp--preset--spacing--40);
			a {
				letter-spacing: 0.125rem;
				text-transform: uppercase;
				color: var(--wp--preset--color--contrast-light);
				text-decoration: none;
				font-family: var(--wp--preset--font-family--highlight);
				
			}
			.sub-menu {
				display: none;
			}
			&:hover {

				box-shadow: 0px 0px 0px 1000rem rgba(0,0,0,.1);
				& > a {
					background-color: var(--wp--preset--color--base);
					box-shadow:  0 0 0 1.25rem var(--wp--preset--color--base);
					border-radius: 1rem;
				}

				& > .sub-menu {
					display: block;
					position: absolute;
					top: 100%;
					left: 3rem;
					right: 3rem;
					background-color: var(--wp--preset--color--base);
					padding: var(--wp--preset--spacing--60) 0;
					border-radius: var(--wp--custom--border--radius--menu);
					li.menu-item {
						display: block;
						margin: 0;
						text-align: center;
						font-size: 2rem;
						a {
							display: inline-block;
							padding: var(--wp--preset--spacing--20) var(--wp--preset--spacing--90);
							text-transform: lowercase;
							font-family: var(--wp--preset--font-family--default)!important;
							color: var(--wp--preset--color--contrast) !important;
							min-width: 15rem;
						}
						&:hover {
							a { color: var(--wp--preset--color--primary) !important }
						}
					}
				}
			} 
		}
	}
} 

.wp-block-pm-menu-fetcher.menu__secondary {
	position: fixed;
	bottom: -0.75rem;
	margin: auto;
	width: 100%;
	font-family: var(--wp--preset--font-family--highlight);
	ul.menu {
		width: fit-content;
		display: flex;
		flex-direction: row;
		justify-content: center;
		gap: var(--wp--preset--spacing--50);
		list-style-type: none;
		margin:auto;
		padding: var(--wp--preset--spacing--40) var(--wp--preset--spacing--50) var(--wp--preset--spacing--50) var(--wp--preset--spacing--50);
		border-radius: 1.5rem;
		background-color:rgba(0,0,0,0.8);
		li.menu-item {
			a {
				text-decoration: none;
				color: white;
				text-transform: uppercase;
				font-size: .8rem;
				letter-spacing: .1rem;
			}
		}
	}
}

footer {
	margin: 0;
}

.mentions-wide {
	max-width: none!important;
}

.wp-block-pm-menu-fetcher.menu__home-footer {

	max-width:none;	
	ul.menu {
		padding: 0;
		margin: 0;
		display: flex;
		list-style: none;
		justify-content: space-between;
		li.menu-item {
			a {
				text-decoration: none;
			}
		}
	}
}